section {
  margin: 0 auto;
  padding: 0 40px;
  width: 85%;
  border-bottom: 1px solid #c4c4c4;
}

section h2 {
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #494949;
  font-size: 2rem;
}

.courses-container {
  display: flex;
  flex-direction: column;

  justify-content: flex-start;
  margin: 60px 0px;
}
.singleCourse,
.conteiner-edu,
.conteiner-exp {
  display: flex;
  flex-direction: row;
  margin: 15px 0;
}
@media (min-width: 868px) {
  .skills-container {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin: 60px 0;
  }
}
@media (max-width: 868px) {
  .skills-container h3 {
    margin-top: 40px;
  }
}
.skills-container h3 {
  text-transform: uppercase;
  letter-spacing: 1px;
}

.skills-container div {
  margin: 0 40px;
}
.skills-container ul {
  list-style-image: url(../img/ulstyle.jpg);
}
.skills-container ul li {
  font-size: 1rem;
  line-height: 2rem;
  letter-spacing: 1px;
}
.course-name,
.exp-name,
.edu-name {
  font-size: 1.1rem;
  word-wrap: break-word;
  padding: 0 10px;
  line-height: 1.6rem;
  width: 25%;
}

.course-description,
.exp-decription,
.edu-decription {
  border-left: 2px solid #5dc3dc;
  padding: 0 10px;
  font-size: 1rem;
  line-height: 1.6rem;
  width: 65%;
}

button.showCertificate {
  font-size: 12px;
  padding: 5px 3px;
  color: #494949;
  border: 1px solid #494949;
}

button.showCertificate:hover {
  color: #fff;
  background-color: #494949;
}
