header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  min-height: 75vh;
  background-image: url(../img/background_2.jpg);
  background-size: cover;
}

#avatar {
  width: 200px;
  height: 200px;
  background-image: url(../img/myAvatar.png);
  background-size: cover;
  border-radius: 150%;
  border: 4px solid #fff;
}

.header-title h2 {
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #fff;
  font-size: 2rem;
  text-align: center;
}
.header-title p {
  max-width: 400px;
  text-align: justify;
  padding: 0 15px;
  line-height: 1.4em;
  letter-spacing: 1px;
  color: #fff;
  font-size: 1rem;
}

@media (max-width: 400px) {
  .header-title p {
    font-size: 0.7rem;
    padding: 15px;
  }
  button {
    transform: scale(0.6);
  }
}

.header-title a {
  color: #fff;
}
.header-title a:hover {
  color: #f59300;
}

.header-title a.active {
  color: #f59300;
}
