.reposContainer h4 {
  color: #5dc3dc;
}
h1 {
  font-size: 26px;
  color: #494949;
  text-transform: uppercase;
  letter-spacing: 3px;
  border-bottom: 1px solid #c4c4c4;
  padding-bottom: 15px;
}

.reposContainer button {
  border: 1px solid #494949;
  color: #494949;
  font-size: 16px;
  letter-spacing: 2px;
  font-weight: 400;
  border-radius: 5px;
}

.reposContainer button:hover {
  color: #fff;
  background-color: #494949;
}

.reposList {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.reposList h4 {
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 16px;
  word-wrap: break-word;
}

.reposList div {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  position: relative;
  box-shadow: 0px 0px 15px 0px rgba(73, 73, 73, 1);
  padding: 15px;
  margin: 15px 0;
  width: 25vw;
  text-align: center;
  background-color: #494949;
  border-radius: 5px;
}

.reposList div p {
  color: #fff;
  margin: 20px 0;
  text-align: justify;
}

.reposList div p:last-child {
  padding: 2px;
  margin: 0;
  border-radius: 5px;
  background-color: #fff;
  text-align: center;
}
.reposList div p:last-child span {
  margin: 0 5px;
}
