nav {
  margin: 0;
  display: block;
  padding: 5px 25px;
  margin-bottom: 25px;
}
nav ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
}
nav ul li {
  display: inline-block;
  margin: 0 10px;
}

nav ul li a {
  text-decoration: none;
  color: #4b4b4b;
  text-transform: uppercase;
  letter-spacing: 4px;
}

nav ul li a:hover {
  color: #ff4200;
}
